<template>
    <div id="menu-panel">
        <div id="menu-panel-bar">
            <p>
                Menu
            </p>

        </div>
        <div id="menu-panel-content">


            <div v-for="page in pages" class="menu-element" :class="selected(page.link)" :key="page.name">
               <p><a :href="page.link" :class="linkEnable">{{page.title}}</a></p>
            </div>

            
        </div>
        
    </div>
</template>
<script>
    import config from '../../../shared/config/config.yaml';
    export default {
        data ()  {
            return {
                currentPath: window.location.hash,
                linkEnable: '',
                gameState: this.$gameState,
                pages: []
            };
        },
        methods: {
            selected(name) {
                
                return name == this.currentPath ? 'selected' : '';
            },
            
        },
        mounted() {
            if (config.simulationMenu) {
                this.pages.push({
                    name : "simulation",
                    link : "#/simulation",
                    title : "Simulation"

                });
            }
            if (config.calibrationMenu) {
                this.pages.push({
                    name : "calibration",
                    link : "#/calibration",
                    title : "Auto Calibration"

                });
            }
            if (config.playMenu) {
                this.pages.push({
                    name : "play",
                    link : "#/",
                    title : "Play"

                });
            }
            if (config.precisionCheckMenu) {
                this.pages.push({
                    name : "precision_check",
                    link : "#/precision_check",
                    title : "Precision Check"

                });
            }
            window.addEventListener('hashchange', () => {
                
                this.currentPath = window.location.hash;
                
                })
        },
        watch:{
            gameState : {
                handler(newGameState, oldGameState) {
                oldGameState.test = "test";
                    this.linkEnable = newGameState.phase == "PLAY" ? 'link-disabled' : '';
                },
                deep: true
            }
        }
    }
</script>
