<template>
    <header>
       <div id="left-header">
            <img id="arena-title" src="@/assets/images/exotec_logo.png"/>
               
            <!-- Exotec logo-->
        </div>

        <div id="play-chrono" v-if="playPhase || endPhase">
            <p v-if="playPhase">
                {{formatTime(minutes)}}:{{formatTime(seconds)}}
            </p>
            <p v-if="endPhase" class="game-time-off">
                Time Off
            </p>
        </div>

        <div id="right-header">
            
                <img src="@/assets/images/exolegend.png" height="50" class="exolegend-logo">
            

        </div>
        <div id="version-bloc">
            <p>
                V{{ version }}+{{ build }}
            </p>
        </div>
    </header>
</template>






<script>
import config from '../../../shared/config/config.yaml';
const DURATION = config.gameDuration;
export default {
    data () {
        return {
            gameState: this.$gameState,
            playPhase: this.$gameState.phase == 'PLAY',
            endPhase: this.$gameState.hase == 'END',
            timeLeft: 0,
            minutes: 0,
            seconds: 0,
            version: config.version,
            build: config.build,

        }
    },
    methods: {
        formatTime(time) {
            let strTime = time < 10 ? "0" + time.toString() : time.toString();
            return strTime;
            
        }
    },
    watch: {
        gameState : {
            handler (newGameState, oldGameState) {
                oldGameState.test = "test";
                this.playPhase = newGameState.phase == 'PLAY';
                this.endPhase = newGameState.phase == 'END';
                console.log(newGameState.phase);
                if(this.playPhase) {
                    
                    this.timeLeft = DURATION - (Date.now()/1000 - newGameState.gameStart);
                    
                    let timeLeft = Math.floor(this.timeLeft);
                    this.minutes = Math.floor(timeLeft/60);
                    this.seconds = Math.floor(timeLeft%60);
                }
                
            },
            deep: true
        }
    }
    
}
</script>




