<template>
  <TopHeader></TopHeader>
  <ContentWindow :gameState="gameState"/>
  
</template>





<script>
// @ is an alias to /src
import ContentWindow from '@/components/ContentWindow.vue';
import TopHeader from "@/components/TopHeader.vue";
import config from '../../shared/config/config.yaml';
/*import { ipcRenderer} from 'electron';*/

const DEBUG_MODE = config.debugMenu;
const FULL_SCREEN = config.fullScreen;

export default {
  name: 'HomeView',
  components: {
    ContentWindow,
    TopHeader
  },
  data() {
    return {
      barcode: "",
      gameState : this.$gameState,
      remoteDebug: false,
      
    }
  },
  methods: {
    scanProcess (e) {
      if(this.gameState.phase == 'REGISTER') {
        let authorizedChars = "ABCDEF1234568790_:";     
        if(e.code =="Enter") {
            let verifi = /^([A-F0-9]{2}:){5}[A-F0-9]{2}_[0-9]{1,3}$/;
            console.log(this.barcode);
            if (verifi.test(this.barcode)){
              this.handleBarcode(this.barcode);
              
            }else{
              alert("Wrong code scanned !!");
            }
            this.barcode = '';
            return;
        }
        if(authorizedChars.includes(e.key))
          this.barcode+=e.key;
      }
    },
    handleBarcode(barcode) {
      
        if (barcode[0] == 'h') {
            barcode = barcode.substr(1);
        }
        let codes = barcode.split('_');
        for(let team of this.gameState.teams){
            
            for (let robot of team.robots) {
              if(robot.selected){
                //cjeck if the location is empty
                let addNew = false;
                if((robot.macAddress==null&&robot.id==null)) {
                  addNew = true;
                  
                }else{
                  //robot already used
                  //alert("This robot is already used. Please delete it if you want to replace it.")
                  if (confirm('Are you sure to replace this robot')) {
                    addNew = true
                    //delete the robot
                    
                  }
                }
                if(addNew) {
                  //check if the robot is already scanned
                  if(!this.isRobotScanned(codes[0], parseInt(codes[1]))) 
                  {
                    this.$gameMaster.removeRobot(robot);
                    robot.scanned = true;
                    robot.macAddress = codes[0];
                    robot.id = parseInt(codes[1]);
                    //add robot
                    this.$gameMaster.addRobot(team.name, robot.id, robot.macAddress)
                  } else {
                    //the robot is already scanned -- exists
                    alert("The robot is already scanned !!");

                  }
                }
              }
            }
        }
      
        
    },
    isRobotScanned(macAddress, id) {
      let exist = false;
      for(let team of this.gameState.teams) {
        for(let robot of team.robots) {
          if(robot.id == id || robot.macAddress == macAddress) {
            exist = true;
          }
        }
      }
      return exist;
    },
    debugRemotePress(e) {
      if(this.gameState.phase == 'REGISTER' && !this.remoteDebug) {
        this.remoteDebug = true;
        let auth = ["ArrowLeft", "ArrowUp", "ArrowRight", "ArrowDown"];
        if(auth.includes(e.code)) {
          
          for(let team of this.gameState.teams){
            
            for (let robot of team.robots) {
              if(robot.selected){
                
                this.$gameMaster.arenaDebugRemote(robot.id, auth.indexOf(e.code));
                console.log("sended ok " + robot.id + "; rmote code="+auth.indexOf(e.code));
              }
            }
          }
        }
      }
    },
    debugRemoteStop(e) {
      if(this.gameState.phase == 'REGISTER') {
        this.remoteDebug = false;
        let auth = ["ArrowLeft", "ArrowUp", "ArrowRight", "ArrowDown"];
        if(auth.includes(e.code)) {
          for(let team of this.gameState.teams){
              for (let robot of team.robots) {
                this.$gameMaster.arenaDebugRemote(robot.id, 4);
              }
            }
          }
      }
    }
    
  },

  created: function () {
    document.title = "Exolegend - ControlScreen"
    //reset game
    this.$gameMaster.resetGame()
    window.addEventListener('keydown',this.scanProcess);
    if (config.arenaDebugRemote) {
      window.addEventListener('keydown',this.debugRemotePress);
      window.addEventListener('keyup',this.debugRemoteStop);
    }
    this.$gameMaster.setState(this.gameState);
    this.$gameMaster.changePhase("REGISTER");
  },
  mounted () {
    if(!DEBUG_MODE){
      //ipcRenderer.invoke('debugMode');
      window.api.debugMode();
      console.log("debug mode");
    }
    if(FULL_SCREEN){
      //ipcRenderer.invoke('fullScreen');
      window.api.fullScreen();
    }
    this.$gameMaster.checkVersion();
  },
  unmounted() {
    //window.addEventListener('keydown',this.scanProcess);
  },
}
</script>




