<template>
    <div id="content" :class="vertical?'vertical':''">
        <MenuPanel></MenuPanel>
        <div id="window">
            <div id="window-bar">
                <p>
                    {{currentName}}
                </p>
            </div>
            <div id="window-content">
                <component :is="currentView" :gameState="gameState"/>
            </div>
        </div>
    </div>
</template>











<script>
import PlayPage from '@/pages/PlayPage.vue'
import MenuPanel from './MenuPanel.vue'
import CalibrationPage from '@/pages/CalibrationPage.vue'
import SimulationPage from '@/pages/SimulationPage.vue'
import config from '../../../shared/config/config.yaml';
import PrecisionCheckPage from '@/pages/PrecisionCheckPage.vue';

const routes = {
  '/': {
    'component' : PlayPage,
    'name' : 'Register & Play'
  },
  '/calibration': {
    'component' : CalibrationPage,
    'name' : 'Auto Calibration'
  },
  '/simulation' : {
    'component' : SimulationPage,
    'name' : 'Simulation'
  },
  '/precision_check' : {
    'component' : PrecisionCheckPage,
    'name' : 'Precision Check'
  }
}
export default {
    data() {
        return {
            currentPath: "#" + config.homePage,
            vertical: config.verticalUI,
        }

    },
    components: {
        PlayPage,
        CalibrationPage,
        MenuPanel,
        SimulationPage,
        PrecisionCheckPage
      
        
    },
    computed : {
        currentView() {
            return routes[this.currentPath.slice(1) || '/']['component'];
        },
        currentName ()  {
            return routes[this.currentPath.slice(1) || '/']['name'];
        }
    },
    props: {
        gameState : {},
    },
    mounted() {
        
        window.addEventListener('hashchange', () => {
            this.currentPath = window.location.hash;
                
            });
  },
    
}
</script>












