import config from '../../../shared/config/config.yaml';
import SocketclientService from '@/services/socketClientService';

// Generate from dockerfile
const gameMasterHost = config.gameMasterHost || "localhost";
const antennaHost = config.antennaHost || "localhost";
const falconHost = config.falconHost || "localhost";

export default class GameMasterServive {
    constructor (gameState) {
        this.socketClient = new SocketclientService(50001, gameMasterHost);
        this.antennaClient = new SocketclientService(50003, antennaHost);
        this.falconClient = new SocketclientService(50002, falconHost);
        this.gameState = gameState;
        this.initEvents();
        
        
    }
    initEvents () {
        setInterval(()=>{
            this.socketClient.send(
                "getGameState", 
                {}, 
                (data) => {
                    this.gameState.gameStart = data.gameStart;
                    this.gameState.phase = data.phase;
                    this.gameState.simulation = data.simulation;
                    //update all status
                    for(let team of data.teams)  {
                        this.updateTeam(team.name, 'score', team.score);
                        this.updateTeam(team.name, 'state', team.state);
                        for(let robot of team.robots) {
                            for(let key of Object.keys(robot)) {
                                
                                this.updateRobot(robot.id, key, robot[key]);
                            }
                            if (robot["undetectedTimeout"])
                            {
                                this.removeRobotByID(robot.id);
                                alert("Timeout - A robot has been removed from the register screen.");
                            }
                        }
                    }
                    
                }
                
            );

        }, 500);
        
    }
    addRobot (teamName, id, macAddress) {
        this.socketClient.send("addRobot", {
            teamName: teamName,
            id: id,
            macAddress: macAddress,
        })
    }
    removeRobotByID(id)
    {
        for (let team of this.gameState.teams) {
            for (let robot of team.robots) {
                if (robot.id == id) {
                    this.removeRobot(robot);
                }
            }
        }
    }
    removeRobot(robot) {
        this.socketClient.send("removeRobot", {
            robotId: robot.id
        });
        let identifier = robot.identifier;
        for(let key of Object.keys(robot)) {
            robot[key] = null;
        }
        robot.identifier = identifier;
        robot.id = null;
        robot.remote = false;
        robot.macAddress = null;
        robot.positionOk = false;
        robot.scanned = false;
        robot.status = "offline";
        robot.selected = false;
        robot.score = 0;
        robot.undetected = true;
    }
    updateSelection(macAddress) {
        this.socketClient.send("updateSelection", {
            macAddress: macAddress
        })
    }
    createTeam(teamName, teamColor) {
        this.socketClient.send("createTeam", {
            teamName: teamName,
            teamColor: teamColor
        })
    }
    changePhase (newPhase) {
        this.gameState.phase = newPhase;
        this.socketClient.send("changePhase", {
            phase: newPhase
        });
        if(newPhase == "REGISTER_SIM") {
            this.falconClient.send("setMode", {
                mode: "sim"
            });
        }else if (newPhase == "REGISTER") {
            this.falconClient.send("setMode", {
                mode: "real"
            });
        }


    }
    setState (gameState) {
        this.gameState = gameState;
        for(let team of gameState.teams) {
            this.createTeam(team.name, team.color)
        }
    }
    updateRobot(id, property, value) {
        for(let team of this.gameState.teams) {
            for(let robot of team.robots) {
                if( robot.id == id) {
                    if (property === "status" && value === "registerElsewhere" && property in robot && robot[property] !== "registerElsewhere")
                    {
                        alert("Robot is already connected to another arena");
                    }
                    robot[property] = value;
                }
            }
        }
    }
    updateTeam(teamName, property, value) {
        for(let team of this.gameState.teams) {
            if (team.name == teamName) team[property] = value;
        }
    }
    resetGame () {
        this.socketClient.send("resetGame", {requestedBy: "arena"});
    }


    getVirtualRobots(callBack) {
        this.antennaClient.send("getVirtuals", {}, (data) => {
            callBack(data.virtuals);
        });
    }
    
    setRobotRemote(robotId, remote) {
        this.socketClient.send("robotRemote", {id: robotId, remote: remote});
    }
    arenaDebugRemote(id, direction) {
        this.socketClient.send("arenaDebugRemote", {id: id, direction: direction});
    }
    checkVersion() {
        this.socketClient.send("versionInfo", {} ,(data) => {
            if(data["uptodate"]=="not") {
                alert("This version of EXOLEGEND is not uptodate, please download the last version " + data["lastVersion"] + "+" + data["lastBuild"] + " to avoid disruption.");
            }
        })
    }
    
}

