<template>
    <div id="calibration-curtain" :class="onProcess()">
        <div id="calibration-curtain-win">
            <div id="calibration-curtain-content">
                <p>
                Processing Calibration ...
                <img src="@/assets/images/loading.gif"/>
            </p>
            </div>
            
        </div>
    </div>
   <div id="calibration-content">
    <div id="calibration-explanation">
        <p>
            1 - Please make sure that the field is empty 
            and maze software is already launched. (no objects or robots on it)
        </p>
        <p>
            2 - Enter the administrator code 
        </p>
        <p>
            3 - Wait until the end of the precision to launch a game.
            It could take some time, please,
            do not close any program while it's running.
        </p>
        
        
    </div>
    <div id="calibration-codepad">
        <div id="calibration-pad-top">
            <div id="calibration-pad-code"><p>{{code}}</p></div>
        </div>
        <div class="calibration-pad-rank">
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('1')"><p>1</p></div>
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('2')"><p>2</p></div>
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('3')"><p>3</p></div>
        </div>
        <div class="calibration-pad-rank">
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('4')"><p>4</p></div>
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('5')"><p>5</p></div>
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('6')"><p>6</p></div>
        </div>
        <div class="calibration-pad-rank">
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('7')"><p>7</p></div>
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('8')"><p>8</p></div>
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('9')"><p>9</p></div>
        </div>
        <div class="calibration-pad-rank">
            <div class="calibration-pad-button" id="calibration-pad-x" @click="(e)=>padButtonClick('x')"><p>X</p></div>
            <div class="calibration-pad-button" @click="(e)=>padButtonClick('0')"><p>0</p></div>
            <div class="calibration-pad-button" id="calibration-pad-ok" @click="(e)=>padButtonClick('ok')"><p>Check</p></div>
        </div>
    </div>
    <!--<button :onclick="calibrate">Calibrer</button>-->
   </div>

</template>





<script>


import config from '../../../shared/config/config.yaml';
const AUTO_RUN = config.precisionCheckAutoRun;
const TIMEOUT = config.precisionCheckAutoRunTimeout;
export default {
    data () {
        return {
            code: '',
            gameState: this.$gameState,
            calbrationOnprocess: false
        }
    },
    
    methods: {
        onProcess() {
            
            return this.calbrationOnprocess ? '': 'calibration-process-off';
        },
        check() {
            this.$gameMaster.changePhase("PRECISION_CHECK");
        },
        padButtonClick(value) {
            
            if(value=="ok") {
                //if the code is right
                if(this.code==config.calibrationCode)
                    this.check();
                else{
                    alert('Wrong code ! ');
                }
                this.code = '';
            }else if (value=="x") {
                this.code = '';
            }else {
                if (this.code.length<4)
                this.code+=value;
            }
        }
    },
    watch: {
        gameState : {
            handler(newGameState, oldGameState) {
                oldGameState.test = "test";
                if(newGameState.phase != "PRECISION_CHECK" && this.calbrationOnprocess) alert("CPrecision check done sucessfully !");
                this.calbrationOnprocess = newGameState.phase == 'PRECISION_CHECK';
                
            },
            deep: true,
        }
    },
    mounted() {
        if(AUTO_RUN) {
            window.setTimeout(
                () => {
                    this.check();
                },
                TIMEOUT*1000
            );
        }
        
    }
}
</script>












