<template >
    <div id="replay-button" :class="replayEnable" @click="restartOnClick">
        <div class="inner-button" >
            <p>Replay</p> 
            <recycle-icon/>
        </div>
                    
    </div>
    <div id="start-button" :class="playEnable" @click="onClick">
        <div class="inner-button" >
            <p>{{buttonText}}</p> 
            <play-icon/>
        </div>
                    
    </div>

</template>



<script>

import PlayIcon from 'vue-material-design-icons/Play.vue';
import config from '../../../shared/config/config.yaml';
import RecycleIcon from 'vue-material-design-icons/Recycle.vue'

const ROBOT_PLAYING = config.robotPlaying
export default {
    data () {
        return {
            gameState: this.$gameState,
            registerPhase: this.$gameState.phase == "REGISTER",
            playPhase: this.$gameState.phase == "PLAY",
            endPhase: this.$gameState.phase == "END",
            simRegisterPhase: this.$gameState.phase == 'REGISTER_SIM',
            playEnable: 'disable',
            replayEnable: 'disable',
            buttonText: "Let's go !",
            devMod: false,
            clickCheck: false,
            autoTimeOut: null,
        }
    },
    props: {
        simulation: Boolean,
        
    },
    components : {
        PlayIcon,
        RecycleIcon,
    },
    watch : {
        gameState:{
                handler(newGameState, oldGameState) {
                    oldGameState.test = "test";
                    let robotReady = 0;
                    for (let team of newGameState.teams) {
                        for(let robot of team.robots) {
                            let condition = robot.scanned && robot.status == "peeredOk" && robot.positionOk && !robot.undetected;
                            //let condition = robot.scanned;
                            if (condition) {
                                robotReady ++;
                            }
                        }
                    }
                    if(this.registerPhase || this.simRegisterPhase)
                        this.playEnable =  robotReady<ROBOT_PLAYING?'disable':'';
                    else
                        this.playEnable = '';
                    this.registerPhase = newGameState.phase == "REGISTER";
                    this.playPhase = newGameState.phase == "PLAY";
                    this.endPhase = newGameState.phase == "END";
                    this.replayEnable = newGameState.phase == "END" && !newGameState.simulation ?'':'disable';  
                    this.simRegisterPhase = this.$gameState.phase == 'REGISTER_SIM';
                    //button text
                    if (this.registerPhase || this.simRegisterPhase) this.buttonText = "Let's go !";
                    if (this.playPhase) this.buttonText = "End game";
                    if (this.endPhase) this.buttonText = "Return to the menu";

                    if (!newGameState.simulation && this.autoTimeOut==null && this.endPhase){
                        //auto return in register mode
                        this.autoTimeOut= window.setTimeout(() => {
                            if (this.endPhase && this.autoTimeOut!=null)
                                this.onClick();
                        }, 30000);
                    }
            },
            deep: true,

        } 
    },
    methods: {
        onClick () {
            //if the button is enabled
            if(this.autoTimeOut!=null) {
                window.clearTimeout(this.autoTimeOut);
                this.autoTimeOut = null;
            }
            
            if(this.playEnable=='' || this.devMod)
            {    
                if(this.registerPhase ||  this.simRegisterPhase)
                    //start game
                    this.$gameMaster.changePhase("PLAY");

                else if(this.playPhase) {
                    //validation dialog
                    var retVal = confirm("Voulez vous vraiment arrêter la partie en cours?");
                    if( retVal ) {
                        this.$gameMaster.changePhase("END");
                    } else {
                        //do nothing
                    }
                }
                
                else {
                    if(!this.simulation) {
                        this.$gameMaster.changePhase("REGISTER");
                    }else {
                        this.$gameMaster.changePhase("REGISTER_SIM");
                    }
                }
            }
        },
        restartOnClick() {
            if(this.autoTimeOut!=null) {
                window.clearTimeout(this.autoTimeOut);
                this.autoTimeOut = null;
            }
            if(this.replayEnable=='' || this.devMod)
            {   
                this.$gameMaster.changePhase("REPLAY");
            }

        },
        onKeydown(e) {
            let key = e.key;
            if (key=="Control") { //active test mode
                this.devMod = true
            }
        },
        onKeyup(e){
            let key = e.key;
            if (key=="Control") { //unactive test mode
                this.devMod = false
            }
        }
    },
    created() {
        window.addEventListener('keydown',this.onKeydown);
        window.addEventListener('keyup',this.onKeyup);
    }

}
</script>



