<template>
    <div class="robot-bloc" :class="[robot.selected?'selected':'',!alive&&(playPhase||endPhase)?'robot-death':'',(!display)&&(playPhase||endPhase)?'robot-off':'']" :id="'robot-'+robot.identifier" @click="selectRobot">
        <!-- register phase  -->
        <div class="robot-name" v-if="registerPhase">
            <p v-if="!robot.scanned" class="robot-state robot-state-nop">
                Scan a robot please
            </p>
            <p v-else class="robot-state robot-state robot-state-ok">
                ID: {{robot.id}}, MAC: {{robot.macAddress}}
            </p>
        </div>

        <!-- SIMULATION REGISTER -------------------------------------- -->
        <div class="robot-name" v-if="simRegisterPhase">
            <p v-if="!robot.scanned" class="robot-state robot-state-nop">
                <select name="virtual-robots" class="robot-selector" :id="'robot-selector-'+robot.identifier">
                    <option value="0_0"> --Please choose a robot-- </option>
                    <option v-for="virtual in virtualRobots" :key="virtual.id" :value="virtual.macAddress + '_' + virtual.id">
                        ID : {{virtual.id}}; ADDRESS : {{virtual.macAddress}}
                    </option>
                    
                </select>
            </p>
            <p v-else class="robot-state robot-state robot-state-ok">
                ID: {{robot.id}} <!--, MAC: {{robot.macAddress}} -->
            </p>
        </div>
        <!-- END SIMULATION REGISTER --------------------------------- -->

        <!-- play phase  -->
        <div class="robot-name"  v-if="playPhase || endPhase">
            <p>
                Robot {{ robot.id }}
            </p>
            <p class="robot-life-state life-alive" v-if="alive">
                ALIVE
            </p>
            <p class="robot-life-state life-dead" v-else>
                DEAD
            </p>
        </div>



        <div class="robot-state" :class="robot.positionOk ? 'robot-state-ok' : 'robot-state-nop'" v-if="registerPhase||simRegisterPhase">
            <p>
                position : 
            </p>
        </div>


        <div class="robot-score" v-if="playPhase || endPhase">
            <p>
                Score : {{ robot.score }}
            </p>
            
        </div>
        <div class="robot-state" :class="robot.status == 'peeredOk' ? 'robot-state-ok' : robot.status == 'registerElsewhere' ? 'robot-state-waiting' : 'robot-state-nop'">
            <p>
                {{registerPhase?'Status : ':'Status :'}}
            </p>
        </div>
        <div class="robot-state" :class="robot.undetected ? 'robot-state-nop' : 'robot-state-ok'" v-if="registerPhase">
            <p>
                Camera detection : 
            </p>
        </div>
        <div class="robot-action-buttons">
            <div class="remove-robot-button" v-if="registerPhase || simRegisterPhase" @click="removeRobot">
                <delete-icon/>
            </div> 
            <div :class="['remote-button', isRemoteActive]" v-if="remoteMode && registerPhase" @click="remoteRobot">
                <controller-classic-icon/>
            </div> 
        </div>
    </div>
</template>

<script>
import DeleteIcon from 'vue-material-design-icons/Delete.vue';
import ControllerClassicIcon from 'vue-material-design-icons/ControllerClassic.vue';
import config from '../../../shared/config/config.yaml';
const REMOTE = config.remote;
export default {
    components : {
        DeleteIcon,
        ControllerClassicIcon,
    },
    props: {
        robot : {},
        simulation: Boolean,
        
    },
    data() {
        return {
            theRobot: this.robot,
            gameState: this.$gameState,
            registerPhase: this.$gameState.phase == 'REGISTER',
            simRegisterPhase: this.$gameState.phase == 'REGISTER_SIM',
            playPhase: this.$gameState.phase == 'PLAY',
            endPhase: this.$gameState.phase == 'END',
            alive: true,
            display: true,
            virtualRobots: [],
            lastPhase: "",
            remote: false,
            undetected: true,
            remoteMode: REMOTE,
        }
    },
    methods: {
        selectRobot () {
            this.deselectAll();
            this.theRobot.selected = true;
            this.$gameMaster.updateSelection(this.theRobot.macAddress)        
        },
        deselectAll() {
            for(let team of this.gameState.teams){
                
                for (let robot of team.robots) {
                    robot.selected = false;
                }
            }  
        },
        removeRobot() {
            this.$gameMaster.removeRobot(this.theRobot);
            this.remote = false;
            this.theRobot.remote = this.remote;
            
        },
        remoteRobot() {

            
            if(this.theRobot.scanned) {
                this.remote = !this.remote;
                this.theRobot.remote = this.remote;
                //send change if scane
                this.$gameMaster.setRobotRemote(this.theRobot.id, this.remote);
            }
        },
        isRobotScanned(macAddress, id) {
            let exist = false;
            for(let team of this.gameState.teams) {
                for(let robot of team.robots) {
                if(robot.id == id || robot.macAddress == macAddress) {
                    exist = true;
                }
                }
            }
            return exist;
            },
        // SIMULATION SEND REGISTER -------------------------------------------------------- -->
        simulationSelectRobot() {
            
            this.selectRobot ();
            
            
            for(let team of this.gameState.teams){
                for (let robot of team.robots) {
                    if(robot.selected){
                            //this.robot.scanned = true;

                        let selected = document.getElementById('robot-selector-'+this.robot.identifier).value;
                        
                        let codes = selected.split('_');
                        if(!this.isRobotScanned(codes[0], parseInt(codes[1]))) {
                            if (parseInt(codes[1])>0){
                                this.$gameMaster.removeRobot(robot);
                                robot.scanned = true;
                                robot.macAddress = codes[0];
                                robot.id = parseInt(codes[1]);
                                this.$gameMaster.addRobot(team.name, robot.id, robot.macAddress);
                            }
                            
                        }else {
                            alert("The robot is already scanned !!");
                        }
                    }
                }
            }
            
        },
        updateVirtualsList() {
            //get list of simulated robots
            this.$gameMaster.getVirtualRobots((virtuals) => {
                this.virtualRobots = virtuals;

            });
        },
        // END SIMULATION SEND REGISTER ---------------------------------------------------- -->
        registerSimListCallbacks() {
            if(this.simRegisterPhase){
            
                const simulatedRobotSelector = document.getElementById('robot-selector-'+this.robot.identifier);
                simulatedRobotSelector.addEventListener("change", this.simulationSelectRobot);
            }
        }
        
    },
    computed: {
        // a computed getter
        isRemoteActive() {
            // `this` points to the component instance
            return this.remote ? 'remote-button-active' : '';
        }
    },
   
    watch: {
        gameState : {
            handler(newGameState, oldGameState) {
                oldGameState;
                this.registerPhase = newGameState.phase == 'REGISTER';
                this.simRegisterPhase = this.$gameState.phase == 'REGISTER_SIM';
                this.playPhase = newGameState.phase == 'PLAY';
                this.endPhase  = newGameState.phase == 'END';
                this.alive = this.theRobot.lifes > 0;
                this.display = this.theRobot.id != null;
                this.undetected = this.theRobot.undetected;
                if((this.lastPhase == 'END' && newGameState.phase=='REGISTER') || (this.lastPhase == 'END' && newGameState.phase=='REGISTER_SIM')) {
                    this.removeRobot();
                    
                }
                this.lastPhase = newGameState.phase
            },
            deep: true,
        }
    },
    created: function () {
        
        if(this.simRegisterPhase) {
                this.updateVirtualsList();
                window.addEventListener('click',this.updateVirtualsList);
            }

        
        
        
    },
    mounted: function () {
        
        this.registerSimListCallbacks()
    },
    updated : function () {
        this.registerSimListCallbacks()
    }
    

    
}
</script>





<style lang="less">
    .robot-bloc {
        display: flex;
        height: 90px;
        background-color: white;
        border-radius: 18px;
        margin: 10px;
        justify-content: space-evenly;
        border: 3px solid #1eb6b2ff;
        p {
            color: #1eb6b2ff;
        }
    }
    .robot-bloc.selected {
        border: 4px solid #9EB861;
    }
    .robot-name {
        margin: auto;
        font-size: 22px;
        display: flex;
        p {
            margin-bottom: 10px;
            margin-top: 7px;
        }
    }
   
    .robot-state::after {
        width: 18px;
        height: 18px;
        content: "";
        background-size: contain;
        background-repeat: no-repeat;
        position: absolute;
        right: -40px;
        bottom: 10px;
    }
    .robot-name p::after {
        bottom: -2px;
    }
    
    .robot-state.robot-state-ok::after {
        background-image: url("@/assets/images/green.png");
    }
    .robot-state.robot-state-nop::after {
        background-image: url("@/assets/images/red.png");
    }
    .robot-state.robot-state-waiting::after {
        background-image: url("@/assets/images/blue.png");
    }
    .robot-state {
        margin:auto;
        position: relative;
        p {
            margin-bottom: 10px;
            margin-top: 7px;
        }
    }
    p.robot-life-state {
        font-weight: bolder;
        margin-left: 20px;

    }
    .robot-death {
        background-color: #b79d76a4;
    }
    p.robot-life-state.life-alive {
        color: #485C19;
    }
    p.robot-life-state.life-dead {
        color: #C34E13;
    }
    .robot-score {
        margin: auto;
        p {
            margin: 0;
        }
    }
    .remove-robot-button {
        margin: auto;
        cursor: pointer;

    }
    .robot-off {
        opacity: 0;
    }
    .robot-action-buttons {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
    }
    .remote-button {
        margin: auto;
        cursor: pointer;
    }
    .remote-button-active {
        background-color: #9EB861;
        color: white;
        width: 40px;
        height: 40px;
        border-radius: 20px;
        padding-top: 8px;
        padding-left: 7px;
    }

    .vertical {
        .robot-bloc {
            flex: 1;
            flex-direction: column;
            height: auto;
            padding: 20px;
        }
        .robot-name {
            font-size: 15px;
            width: 100%;
        }
        .robot-state {
            width: calc(100% - 80px);
        }
    }
</style>