<template>
    <div class="team-bloc">
        <div class="team-name">
            <p>
                {{team.name}}
            </p>

        </div>
        <div class="team-robot-list">
            <RobotBloc v-for="robot in team.robots" :robot="robot"  :key="robot.identifier" :simulation="simulation"/>
        </div>
        
    </div>
    <div class="team-bottom-bar" v-if="gameState.phase == 'PLAY' || gameState.phase == 'END'">
        <p :class="['team-result',' team-result-' + isWinner()]" v-if="gameState.phase=='END'">
             <!--{{ isWinner().toUpperCase() }}-->
        </p>
        <p class="team-score">
            TOTAL SCORE: {{ theTeam.score }}
        </p>
           

        </div>
</template>
<script>
import RobotBloc from '@/components/RobotBloc.vue';

export default {

    components: {
        RobotBloc,
    },
    data () {
        return {
            gameState: this.$gameState,
            theTeam: this.team
        };
    },
    props : {
        team : {},
        simulation: Boolean,
    },
    methods: {
        isWinner() {
            
            let state = "loser";
            for (let team of this.gameState.teams) {
                if (team.id == this.theTeam.id) {
                    state = team.state == "WINNER" ?"winner":"loser";
                    console.log(team.state)
                }
            }
            return state;
                
        },
    },
}
</script>



